<template>
  <el-dialog
    title="Install MetaMask"
    center
    style="font-weight: bold"
    :visible.sync="dialogVisiable"
    custom-class="dialog-style"
    width="400px"
    :fullscreen="isSmallScreen"
  >
    <div class="detail">
      <img class="image" src="../assets/img/metamask.png" alt="metamask" />
      <div class="warn">
        Looks like MetaMask plugin is missing on your browser, you need to
        connect to MetaMask wallet to continue. Please refresh the page after
        your installation.
      </div>
      <rect-button
        class="create"
        width="200px"
        @click="openLink('https://metamask.io/download.html')"
      >
        Install</rect-button
      >
    </div>
  </el-dialog>
</template>

<script>
import RectButton from "../components/RectButton.vue";

export default {
  props: {
    showDialog: { type: Boolean, default: false }
  },
  components: {
    RectButton
  },
  computed: {
    dialogVisiable: {
      get: function() {
        return this.showDialog;
      },
      set: function(value) {
        this.$emit("onClose", false);
        return value;
      }
    },
    isSmallScreen() {
      return window.innerWidth < 450;
    }
  },
  methods: {
    openLink(e) {
      window.open(e);
      this.$emit("onClose", false);
    }
  }
};
</script>

