<template>
  <div class="back-button">
    <rect-button width="48px" @click="back"
      ><i class="el-icon-back"></i
    ></rect-button>
  </div>
</template>

<script>
import RectButton from "./RectButton.vue";
export default {
  props: {
    link: { type: String, default: "/" }
  },
  components: {
    RectButton
  },
  methods: {
    back() {
      if (
        this.$routerHistory.hasPrevious() &&
        this.$routerHistory.previous().path === this.link
      ) {
        this.$router.go(-1);
      } else {
        this.$router.replace(this.link);
      }
    }
  }
};
</script>

<style scoped>
.back-button {
  margin-right: 1em;
}
</style>
