<template>
  <div class="main">
    <auto-hedaer>
      <template v-slot:left>
        <back-button :link="`/workspace/${workspaceId}`"></back-button>
      </template>
      <h1 class="hidden-xs-only">{{ workspace.name }}</h1>
      <template v-slot:right><div style="padding: 1px"></div></template>
    </auto-hedaer>
    <div class="body" v-loading="loading">
      <div class="left-panel">
        <div class="title">{{ proposal.title }}</div>
        <div class="about-panel hidden-lg-and-up" v-show="!loading">
          <div class="right-about">
            <div class="state">
              <div class="title">About</div>
              <div
                class="status"
                :style="{ background: !showResult ? '#21b66f' : '#ff4231' }"
              >
                {{ proposal.status }}
              </div>
            </div>
            <div class="state">
              <div class="content">From</div>
              <div style="text-align: right">
                {{ dateString(proposal.start) }}
              </div>
            </div>
            <div class="state">
              <div class="content">To</div>
              <div style="text-align: right">
                {{ dateString(proposal.end) }}
              </div>
            </div>
            <div class="state">
              <div class="content">Snapshot</div>
              <div>{{ proposal.snapshot }}</div>
            </div>
          </div>
          <div class="right-about" v-show="!showResult">
            <div class="state">
              <div class="title">Your token balance</div>
              <div class="network">{{ chainName }}</div>
            </div>
            <div class="balance" v-loading="loadBalance">
              <div
                class="unavailable"
                v-if="tokenBalance === '' && !loadBalance"
              >
                Unavailable
              </div>
              <div class="amount">
                <div class="result">{{ tokenBalance.balance }}</div>
                <div>{{ tokenBalance.symbol }}</div>
              </div>
            </div>
          </div>
          <div class="right-about" v-show="showResult">
            <div class="state">
              <div class="title">Result</div>
              <div class="network">{{ chainName }}</div>
            </div>
            <div
              class="balance"
              v-for="(result, index) in finalResult"
              :key="index"
            >
              <div class="result">{{ result }}</div>
            </div>
          </div>
        </div>
        <div class="content" v-html="proposal.content"></div>
        <div v-loading="voting">
          <content-panel
            class="panel"
            :title="disableVote || voted ? 'You have voted' : 'Cast your vote'"
            v-show="!showResult && !loading"
          >
            <rect-button
              class="item"
              v-for="(item, index) in proposal.options"
              :key="index"
              @click="vote(index, proposal.id)"
              width="80%"
              :disabled="disableVote || voted"
            >
              {{ item }}
            </rect-button>
          </content-panel>
        </div>
        <content-panel
          class="panel"
          title="Results"
          v-show="showResult"
          v-loading="loadResult"
        >
          <div id="echarts"></div>
        </content-panel>
      </div>
      <div class="right-panel hidden-md-and-down" v-show="!loading">
        <div class="right-about">
          <div class="state">
            <div class="title">About</div>
            <div
              class="status"
              :style="{ background: !showResult ? '#21b66f' : '#ff4231' }"
            >
              {{ proposal.status }}
            </div>
          </div>
          <div class="state">
            <div class="content">From</div>
            <div>{{ dateString(proposal.start) }}</div>
          </div>
          <div class="state">
            <div class="content">To</div>
            <div>{{ dateString(proposal.end) }}</div>
          </div>
          <div class="state">
            <div class="content">Snapshot</div>
            <div>{{ proposal.snapshot }}</div>
          </div>
        </div>
        <div class="right-about" v-show="!showResult">
          <div class="state">
            <div class="title">Your token balance</div>
            <div class="network">{{ chainName }}</div>
          </div>
          <div class="balance" v-loading="loadBalance">
            <div class="unavailable" v-if="tokenBalance === '' && !loadBalance">
              Unavailable
            </div>
            <div class="amount">
              <div class="result">{{ tokenBalance.balance }}</div>
              <div>{{ tokenBalance.symbol }}</div>
            </div>
          </div>
        </div>
        <div class="right-about" v-show="showResult">
          <div class="state">
            <div class="title">Result</div>
            <div class="network">{{ chainName }}</div>
          </div>
          <div
            class="balance"
            v-for="(result, index) in finalResult"
            :key="index"
          >
            <div class="result">{{ result }}</div>
          </div>
        </div>
      </div>
      <metamask-dialog
        :showDialog="showMetamaskDialog"
        @onClose="showMetamaskDialog = false"
      ></metamask-dialog>
      <network-dialog
        :showDialog="showNetworkDialog"
        @onClose="showNetworkDialog = false"
      ></network-dialog>
    </div>
  </div>
</template>

<script>
import {
  callVote,
  getProposalOptions,
  getBalance,
  checkMetaMask
} from "../matalab/witness.js";
import AutoHedaer from "../components/AutoHeader.vue";
import RectButton from "../components/RectButton.vue";
import ContentPanel from "../components/ContentPanel.vue";
import MetamaskDialog from "../components/MetamaskDialog.vue";
import NetworkDialog from "../components/NetworkDialog.vue";
import Network from "../matalab/global";
import BackButton from "../components/BackButton.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  data() {
    return {
      fullscreenLoading: false,
      finalResult: [],
      showMetamaskDialog: false,
      showNetworkDialog: false,
      tokenBalance: "",
      chainName: Network.chainInfo.name,
      loadResult: false,
      loading: false,
      loadBalance: false,
      disableVote: false,
      voting: false,
      workspaceId: ""
    };
  },
  components: {
    AutoHedaer,
    RectButton,
    ContentPanel,
    MetamaskDialog,
    NetworkDialog,
    BackButton
  },
  async mounted() {
    this.loading = true;
    const proposal = this.$route.params.proposal;
    const proposalId = this.$route.params.proposalId;
    this.workspaceId = (proposalId >> 20) << 20;
    await this.$store.dispatch("changeProposal", {
      id: proposalId,
      proposal: proposal
    });
    this.loading = false;
    if (this.proposal.status === "Active") {
      this.loadBalance = true;
      this.tokenBalance = await this.getTokenBalance();
      this.loadBalance = false;
    } else if (this.proposal.status === "Closed") {
      this.loadResult = true;
      let voteResult;
      if (this.proposal.result) {
        voteResult = this.proposal.result;
      } else {
        voteResult = await getProposalOptions(
          this.proposal.id,
          this.workspace.contract,
          this.proposal.options
        );
      }
      this.loadResult = false;
      this.finalResult = voteResult.finalResult || [];
      if (voteResult.result && voteResult.result.length > 0) {
        this.drawLine(voteResult.result);
        this.$store.dispatch("updateProposal", {
          id: this.proposal.id,
          voteResult: voteResult
        });
      }
    }
  },

  computed: {
    showResult() {
      return this.proposal.status === "Closed";
    },
    voted() {
      if (this.accountVotes.length > 0) {
        for (let vote of this.accountVotes) {
          if (vote.proposalId === this.proposal.id) {
            return true;
          }
        }
        return false;
      } else {
        return false;
      }
    },
    ...mapGetters({
      proposal: "activeProposal",
      workspace: "activeWorkspace",
      accountVotes: "accountVotes"
    })
  },

  methods: {
    async vote(option) {
      this.showNetworkDialog = false;
      this.showMetamaskDialog = false;
      const installed = await checkMetaMask();
      if (installed) {
        let chainId = await window.ethereum.request({ method: "eth_chainId" });
        if (parseInt(chainId) === Network.chainInfo.id) {
          this.startVote(this.proposal.id, option);
        } else {
          this.showNetworkDialog = true;
        }
      } else {
        this.showMetamaskDialog = true;
      }
    },

    async getTokenBalance() {
      const installed = await checkMetaMask();
      if (installed) {
        try {
          let balance = await getBalance(this.workspace.contract);
          if (balance) {
            return balance;
          } else return "";
        } catch (e) {
          return "";
        }
      } else {
        return "";
      }
    },

    update() {
      let myChart = this.$echarts.init(document.getElementById("echarts"));
      myChart.setOption({
        series: [
          {
            name: "Result",
            type: "bar",
            data: [2, 3]
          }
        ]
      });
    },

    errorNotify(message) {
      this.$notify.error({ title: "Error", message: message, offset: 100 });
    },

    successNotify(message) {
      this.$notify({
        title: "Success",
        message: message,
        offset: 100,
        type: "success"
      });
    },

    dateString(timestamp) {
      const date = new Date(parseInt(timestamp) * 1e3);
      let dateString = date.toLocaleString();
      if (dateString === "Invalid Date") {
        dateString = "Unknown";
      }
      return dateString;
    },

    async startVote(id, option) {
      this.disbleVote = true;
      this.voting = true;
      let result = callVote(id, option);
      result
        .then(res => {
          console.debug(res);
          if (res.status == 200) {
            this.voting = false;
            const resultCode = res.data.result;
            switch (resultCode) {
              case 0:
                this.successNotify("Vote successfully!");
                this.disbleVote = true;
                this.saveVoteHistory({
                  proposalId: this.proposal.id,
                  title: this.proposal.title
                });
                break;
              case 1:
                this.errorNotify("Signature error");
                break;
              case 3:
                this.errorNotify("You have voted before");
                this.disbleVote = true;
                this.saveVoteHistory({
                  proposalId: this.proposal.id,
                  title: this.proposal.title
                });
                break;
              case 9:
                this.errorNotify("You don't have enough token to vote");
                break;
              default:
                this.errorNotify("Vote failed");
                break;
            }
          }
        })
        .catch(err => {
          console.debug(err);
          this.voting = false;
          this.errorNotify("Vote failed");
        });
    },

    getResult(result) {
      if (result.length === 0) {
        return [];
      } else {
        let maxVote = Math.max(...result);
        if (maxVote === 0) {
          return ["No result"];
        }
        let finalResult = [];
        for (let i = 0; i < this.proposal.options.length; i++) {
          if (result[i] == maxVote) {
            finalResult.push(this.proposal.options[i]);
          }
        }
        return finalResult;
      }
    },

    drawLine(showData) {
      let myChart = this.$echarts.init(document.getElementById("echarts"));
      myChart.setOption({
        tooltip: {},
        xAxis: {
          data: this.proposal.options,
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            color: "#000000",
            fontSize: 20,
            fontWeight: 400
          },
          axisLine: {
            show: false
          }
        },
        yAxis: {
          axisLabel: {
            formatter: function() {
              return "";
            }
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          }
        },
        series: [
          {
            name: "Results",
            type: "bar",
            data: showData,
            barWidth: 68,
            label: {
              show: true,
              position: "top",
              fontSize: 20
            },
            itemStyle: {
              borderRadius: [10, 10, 10, 10],
              color: function(params) {
                var colorList = [
                  "#EF532C",
                  "#376FF9",
                  "#C1232B",
                  "#B5C334",
                  "#FCCE10",
                  "#E87C25",
                  "#27727B",
                  "#FE8463",
                  "#9BCA63",
                  "#FAD860",
                  "#F3A43B",
                  "#60C0DD",
                  "#D7504B",
                  "#C6E579",
                  "#F4E001",
                  "#F0805A",
                  "#26C0C0"
                ];
                return colorList[params.dataIndex];
              }
            }
          }
        ]
      });
    },

    ...mapActions(["saveVoteHistory", "successNotify"])
  }
};
</script>

