<template>
  <el-dialog
    title="Change Network"
    center
    style="font-weight: bold"
    :visible.sync="dialogVisiable"
    custom-class="dialog-style"
    width="400px"
    :fullscreen="isSmallScreen"
  >
    <div class="detail">
      <img class="image" src="../assets/img/metamask.png" alt="metamask" />
      <div class="warn">
        Please switch your metamask network to {{ chainName }}.
      </div>
      <rect-button class="create" width="200px" @click="close">
        Close</rect-button
      >
    </div>
  </el-dialog>
</template>

<script>
import Network from "../matalab/global";
import RectButton from "../components/RectButton.vue";

export default {
  props: {
    showDialog: { type: Boolean, default: false }
  },
  data() {
    return {
      address: Network.automataAddress,
      chainName: Network.chainInfo.name
    };
  },
  components: {
    RectButton
  },
  computed: {
    dialogVisiable: {
      get: function() {
        return this.showDialog;
      },
      set: function(value) {
        this.$emit("onClose", false);
        return value;
      }
    },
    isSmallScreen() {
      return window.innerWidth < 450;
    }
  },
  methods: {
    close() {
      this.$emit("onClose", false);
    }
  }
};
</script>

